.welcome__page__buttons:hover{
  cursor: pointer;
}
@keyframes spin {
  from {
      transform:rotate(0deg);
      opacity: 0.7;
  }
  to {
      transform:rotate(360deg);
      opacity: 0.5;
  }
}
.heart-shape{
  position: relative;
  width: 100px;
  height: 100px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  background-color: #e76c6c;


  display: flex;
  align-items: center;
  justify-content: center;


  background-color: #e76c6c;
  animation-name: spin;
  animation-duration: 10000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear; 
}
.heart-shape:before,
.heart-shape:after{
  position: absolute;
  width: 100px;
  height: 100px;
  content: '';
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -o-border-radius: 50%;
  border-radius: 50%;
  background-color: #e76c6c;


  display: flex;
  align-items: center;
  justify-content: center;
}
.heart-shape:before{
  bottom: 0px;
  left: -50px;
}
.heart-shape:after{
  top: -50px;
  right: 0px;
}
